.animation-container {
  position: relative;
  width: 100%;
  height: 354px; /* Высота контейнера анимации */
  background: #fff;
  overflow: hidden;
}
.animation-title {
  color: #fff;
  -webkit-text-stroke: 3px #000;
  font-family: TT Firs Neue-700;
  font-size: 100px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}  
.text-animation {
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
}
.text-animation-title {
  margin-top: 3rem;
}
.text-animation-title-2 {
  margin-top: 11rem;
}
@media screen and (max-width: 768px) {
  .animation-container {
    height: 170px;
  }
  .animation-title {
    font-size: 42px;
  }
  .text-animation-title {
    margin-top: 1rem;
  }
  .text-animation-title-2 {
    margin-top: 6rem;
  }
}